  --color-midnight: #2a2d3a;
  --color-rust: #8b5a3c;

  /* Fonts */
  --serif: 'Cormorant Garamond', serif;
  --sans: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;

  /* Shadows */
  --shadow-soft: 0 4px 24px rgba(201, 169, 110, 0.08);
  --shadow-medium: 0 8px 32px rgba(201, 169, 110, 0.12);
  --shadow-strong: 0 12px 48px rgba(201, 169, 110, 0.16);
}

.dark {
  --background: #0f0e0d;
  --background-light: #1a1816;
  --foreground: #e8e3d8;
  --foreground-muted: #a39a8b;

  --radial-start: #0f0a06;
  --radial-end: #000000;
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: var(--sans);
  font-weight: 300;
  letter-spacing: -0.01em;
  overflow-x: hidden;
  position: relative;
}

/* Vintage paper texture overlay */
body::before {
  content: '';
  position: fixed;
  inset: 0;
  background-image:
    url("data:image/svg+xml,%3Csvg width='100' height='100' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' /%3E%3CfeColorMatrix type='saturate' values='0'/%3E%3C/filter%3E%3Crect width='100' height='100' filter='url(%23noise)' opacity='0.03'/%3E%3C/svg%3E");
  pointer-events: none;
  z-index: 1;
  mix-blend-mode: overlay;
}

/* Content wrapper to ensure proper stacking */
body > * {
  position: relative;
  z-index: 2;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--serif);
  font-weight: 400;
  letter-spacing: -0.02em;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Refined scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(212, 175, 55, 0.3);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(212, 175, 55, 0.5);
}

/* Selection */
::-moz-selection {
  background: rgba(212, 175, 55, 0.2);
  color: var(--foreground);
}
::selection {
  background: rgba(212, 175, 55, 0.2);
  color: var(--foreground);
}

/* Smooth fade in */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.6s ease-out;
}

/* Elegant underline effect */
.elegant-link {
  position: relative;
  text-decoration: none;
}

.elegant-link::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--accent);
  transition: width 0.3s ease;
}

.elegant-link:hover::after {
  width: 100%;
}

